import React, {useState, useEffect} from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';

import VendorCustomerModal from './VendorCustomerModal';
import useAuth from 'hooks/useAuth';

// material-ui
import { useTheme } from '@material-ui/core/styles';
import {
    AppBar as MuiAppBar,
    Box,
    Button,
    Container,
    Drawer,
    IconButton,
    Link,
    List,
    ListItemButton,
    ListItemIcon,
    ListItemText,
    Stack,
    Toolbar,
    Typography,
    useScrollTrigger
} from '@material-ui/core';

// project imports
import Logo from 'ui-component/Logo';



// assets
import { IconBook, IconUserCheck, IconCreditCard, IconDashboard, IconHome2, IconPhoneCheck, IconNotebook, IconTriangleSquareCircle, IconCircleDashed} from '@tabler/icons';
import MenuIcon from '@material-ui/icons/Menu';

// elevation scroll
function ElevationScroll(props) {
    const { children, window } = props;
    const theme = useTheme();
    const borderColor = theme.palette.mode === 'dark' ? theme.palette.dark.dark : theme.palette.grey[200];

    const trigger = useScrollTrigger({
        disableHysteresis: true,
        threshold: 0,
        target: window ? window() : undefined
    });

    return React.cloneElement(children, {
        elevation: trigger ? 2 : 0,
        style: {
            backgroundColor: theme.palette.background.default,
            borderBottom: trigger ? 'none' : '1px solid',
            borderColor: trigger ? '' : borderColor,
            color: theme.palette.text.dark
        }
    });
}

// ===========================|| MINIMAL LAYOUT APP BAR ||=========================== //

const AppBar = ({ ...others }) => {

    const navigate = useNavigate();
    const { isCustomerLoggedIn , isLoggedIn} = useAuth();
    const { user } = useAuth();
    const [drawerToggle, setDrawerToggle] = React.useState(false);
    const drawerToggler = (open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setDrawerToggle(open);
    };

    let { where } = useParams()


    const goHome = () => {
        if (window.location.href === 'https://store.thegoodmarket.io/') {
            window.location.href = 'https://thegoodmarket.io'
        } else {
            navigate('/')
        }
        
    }

    const status = localStorage.getItem('status')
    const goDash = () => {
        navigate('/login')
    }
    const trackOrder = () => {
        if (isCustomerLoggedIn === true){
        navigate('/ordertracker')}
        else {
        console.log("LOGINNNNNNNNNNN")}
    }

    const handleLogin = () => {
        navigate('/login')
    }

    const [open, setOpen] = React.useState(false);
        const handleClickOpenDialog = () => {
            setOpen(true);
        };
        const handleCloseDialog = () => {
            setOpen(false);
        };


    let username = ''
    try {
        username = user.username
        
    }catch (error) {
        console.log("No Username")
    }

    const openSDGs = () => {
        window.location.href = 'https://thegoodmarket.io/sdgs-in-zimbabwe'
    }

    const openAfCFTA = () => {
        window.location.href = 'https://thegoodmarket.io/afcta'
    }

    const openResources = () => {
        window.location.href = 'https://thegoodmarket.io/resources'
    }

    const openContact = () => {
        window.location.href = 'https://thegoodmarket.io/contact'
    }

    return (
        <ElevationScroll {...others}>
            <MuiAppBar>
                <Container style={{/* marginTop: '-10px', */ /* marginBottom: '-10px' */}}>
                    <Toolbar>
                        <Typography component="div" sx={{ flexGrow: 1, textAlign: 'left' }}>
                            <Logo />
                        </Typography>
                        <Stack direction="row" sx={{ display: { xs: 'none', md:'block', lg:'block', sm: 'none' } }} spacing={2}>
                            <Button style={{fontSize: '18px'}} color="inherit" onClick={goHome}>
                                Home
                            </Button>
                            <Button style={{fontSize: '18px'}} color="inherit">
                                <a 
                                href="https://thegoodmarket.io/sdgs-in-zimbabwe" 
                                target="_blank"
                                style={{textDecoration:'none', color: '#000'}}
                                >SDGs</a>
                                
                            </Button>
                            <Button style={{fontSize: '18px'}} color="inherit">
                                <a 
                                href="https://thegoodmarket.io/afcta" 
                                target="_blank"
                                style={{textDecoration:'none', color: '#000'}}
                                >AfCFTA</a>
                            </Button>
                            <Button style={{fontSize: '18px'}} color="inherit" >
                                <a 
                                href="https://thegoodmarket.io/resources" 
                                target="_blank"
                                style={{textDecoration:'none', color: '#000'}}
                                >Resources</a>
                            </Button>
                            <Button style={{fontSize: '18px'}} color="inherit" onClick={goHome}>
                                <a 
                                href="https://thegoodmarket.io/contact" 
                                target="_blank"
                                style={{textDecoration:'none', color: '#000'}}
                                >Contact</a>
                            </Button>
                            {status === true && <Button style={{fontSize: '18px'}} color="inherit" onClick={goDash} /* component={RouterLink} to="/default/dashboard" */ >
                                Dashboard
                            </Button>}
                            
                            {/* 
                            <Button color="inherit" component={Link} href="https://codedthemes.gitbook.io/berry" target="_blank">
                                Documentation
                            </Button> */}
                            { isCustomerLoggedIn ? '' :<Button
                                style={{borderRadius: '35px', minWidth: '150px', margin: '15px', color: '#fff', fontSize: '18px'}}
                                disableElevation
                                variant="contained"
                                color="secondary"

                                /* onClick={handleClickOpenDialog} */
                                onClick={handleLogin}
                            >
                                {user ? username : 'Login'}
                            </Button>}
                            {isCustomerLoggedIn && 
                            <Button
                                style={{fontSize: '18px'}} 
                                disableElevation
                                variant="contained"
                                style={{backgroundColor: '#f68e1f'}}

                                onClick={trackOrder}
                            >
                                Customer Options
                            </Button>
                            }
                        </Stack>
                        <VendorCustomerModal open={open} handleCloseDialog={handleCloseDialog} />
                        <Box sx={{ display: { xs: 'block', sm: 'block', md: 'none', lg:'none' } }}>
                            <IconButton color="inherit" onClick={drawerToggler(true)}>
                                <MenuIcon />
                            </IconButton>
                            <Drawer anchor="top" open={drawerToggle} onClose={drawerToggler(false)}>
                                <div
                                    sx={{
                                        width: 'auto'
                                    }}
                                    role="presentation"
                                    onClick={drawerToggler(false)}
                                    onKeyDown={drawerToggler(false)}
                                >
                                    <List>
                                        <ListItemButton component={Link} href="/">
                                            <ListItemIcon>
                                                <IconHome2 />
                                            </ListItemIcon>
                                            <ListItemText primary="Home" />
                                        </ListItemButton>
                                        <ListItemButton  onClick={openSDGs}>
                                            <ListItemIcon>
                                                <IconCircleDashed />
                                            </ListItemIcon>
                                            <ListItemText primary="SDGs" />
                                        </ListItemButton>
                                        <ListItemButton onClick={openAfCFTA}>
                                            <ListItemIcon>
                                                <IconTriangleSquareCircle />
                                            </ListItemIcon>
                                            <ListItemText primary="AfCFTA" />
                                        </ListItemButton>
                                        <ListItemButton onClick={openResources}>
                                            <ListItemIcon>
                                                <IconNotebook />
                                            </ListItemIcon>
                                            <ListItemText primary="Resources" />
                                        </ListItemButton>
                                        <ListItemButton onClick={openContact}>
                                            <ListItemIcon>
                                                <IconPhoneCheck />
                                            </ListItemIcon>
                                            <ListItemText primary="Contact" />
                                        </ListItemButton>
                                        {/* <ListItemButton component={RouterLink} to="/login">
                                            <ListItemIcon>
                                                <IconDashboard />
                                            </ListItemIcon>
                                            <ListItemText primary="Dashboard" />
                                        </ListItemButton> */}
                                         <ListItemButton onClick={handleLogin} /* component={RouterLink} to="/login" target="_blank" */>
                                            <ListItemIcon>
                                                <IconUserCheck />
                                            </ListItemIcon>
                                            <ListItemText primary={isCustomerLoggedIn ? username : 'Login'} />
                                        </ListItemButton>
                                        { isCustomerLoggedIn && <ListItemButton onClick={trackOrder}  /* component={RouterLink} to="/login" target="_blank" */>
                                            <ListItemIcon>
                                                <IconBook />
                                            </ListItemIcon>
                                            <ListItemText primary='Customer Options' />
                                        </ListItemButton>}
                                        {/*
                                        <ListItemButton component={Link} href="https://codedthemes.gitbook.io/berry" target="_blank">
                                            <ListItemIcon>
                                                <IconBook />
                                            </ListItemIcon>
                                            <ListItemText primary="Documentation" />
                                        </ListItemButton>
                                        <ListItemButton
                                            component={Link}
                                            href="https://material-ui.com/store/items/berry-react-material-admin/"
                                            target="_blank"
                                        >
                                            <ListItemIcon>
                                                <IconCreditCard />
                                            </ListItemIcon>
                                            <ListItemText primary="Purchase Now" />
                                        </ListItemButton> */}
                                    </List>
                                </div>
                            </Drawer>
                        </Box>
                    </Toolbar>
                </Container>
            </MuiAppBar>
        </ElevationScroll>
    );
};

export default AppBar;
