import PropTypes from 'prop-types';
import React from 'react';
import { Link } from 'react-router-dom';

// material-ui
import { Button, Dialog, Divider, DialogTitle, IconButton, Grid, Stack, Typography, Zoom, useMediaQuery } from '@material-ui/core';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import { gridSpacing } from 'store/constant';

// third-party
// third-party
import { Chance } from 'chance';

// assets
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import HighlightOffTwoToneIcon from '@material-ui/icons/HighlightOffTwoTone';

const chance = new Chance();

const Transition = React.forwardRef((props, ref) => <Zoom ref={ref} {...props} />);

// ===========================|| CHECKOUT CART - DISCOUNT COUPON CODE ||=========================== //

const VendorCustomerModal = ({ open, handleCloseDialog }) => {
    const matchDownMD = useMediaQuery((theme) => theme.breakpoints.down('sm'));

    return (
        <Dialog
            open={open}
            onBackdropClick={handleCloseDialog}
            TransitionComponent={Transition}
            keepMounted
            aria-labelledby="alert-dialog-slide-title"
            aria-describedby="alert-dialog-slide-description"
            maxWidth="md"
            sx={{
                '& .MuiDialog-paper': {
                    p: 0
                }
            }}
        >
            <MainCard>
                <DialogTitle>LogIn</DialogTitle>
                <Grid container>
                <Button
                    component={Link} to="/login" /* target="_blank" */
                    disableElevation
                    variant="contained"
                    color="secondary"
                    style={{marginLeft: '15px', marginRight:'8px'}}
                >
                    Vendor
                </Button>
                <Button
                    component={Link}
                    to="/pages/login/login1"
                    disableElevation
                    variant="contained"
                    color="secondary"
                >
                    Customer
                </Button>
                </Grid>
                
            </MainCard>
        </Dialog>
    );
};

VendorCustomerModal.propTypes = {
    open: PropTypes.bool
};

export default VendorCustomerModal;
